.screen-1 {
  .intro {
    display: flex;
    margin-bottom: 40px;

    &-hyphen {
      display: none;
      box-sizing: border-box;
      width: 20%;
      padding: 0 20px;
      stroke-width: 3px;
      fill: none;
      stroke: currentColor;

      @include bp($medium) {
        display: block;
        transform-origin: 0% 50%;
        transform: scaleX(0);
      }

      @include bp(1350px) {
        width: 100px;
      }
    }

    &-content {
      margin: 0;
      width: 80%;
    }
  }

  .bubble-1 {
    position: absolute;
    z-index: -1;
    top: -10%;
    left: 20px;
    width: 20%;

    @include bp(1350px) {
      left: -5%;
    }

    .background {
      fill: $color-peach;
    }

    .image {
      width: 100%;
      transform: translate(-10%, 20%);
    }
  }

  .bubble-2 {
    position: absolute;
    z-index: -1;
    bottom: -10%;
    right: 20px;
    width: 26%;

    @include bp(1350px) {
      right: -10%;
    }

    .background {
      fill: $color-lavender;
    }

    .image {
      width: 100%;
      transform: translate(20%, 20%);
    }
  }

  .doodle-1-wrapper {
    top: 60%;
    width: 11%;
    height: 11%;
    left: 10%;
    position: absolute;
    z-index: -1;
    transform-origin: 50% 50%;
  }

  .doodle-1 {
    width: 100%;
    height: 100%;
    animation: doodle-rotate 5s $doodles-easing infinite alternate;
  }

  .doodle-2-wrapper {
    position: absolute;
    z-index: -1;
    top: 10%;
    right: 10%;
    width: 11%;
    height: 11%;
    transform-origin: 50% 50%;
  }

  .doodle-2 {
    width: 100%;
    height: 100%;
    animation: doodle-rotate 5s $doodles-easing infinite alternate-reverse;
    animation-delay: 3s;
  }
}

@keyframes doodle-rotate {
  from {
    transform: rotate(-90deg);
  }
  to {
    transform: rotate(0deg);
  }
}
